


.loginContainer{
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    width: 25%;
    height: 250px;
 
    box-sizing: content-box;
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
    opacity:0.5;
    border-radius: 14px;
    animation: mymove 2s ;
    animation-timing-function: ease-in-out;
    
  animation-fill-mode: backwards;

}
@keyframes mymove {
     from { 
     transform:scale(0.5)}
  to { background: linear-gradient(to right, #0f2027, #203a43, #2c5364);}
}
.box{
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    width: 90%;
    padding-top: 12px;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-top:0;
    border-bottom:0;
    border-right:0;
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);

}
.button{
    padding-bottom: 10px;
    padding-top: 10px;
    border: none;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    width: 90%;
    cursor:pointer;

}
.nameErr{
    display:none;
    font-size:12px;
}
input:focus {
  border: 1px solid #555;
}
input{
    border:none;
}
h2{
    color:whitesmoke;
}

@media screen and (max-width: 576px){
    .loginContainer{
    margin-top: 150px;
    margin-left: auto;
    margin-right: auto;
    width: 55%;
    height: 250px;


}


}