.topBar{
  top:0;
    width :100%;
    height: 70px;
    background-color: white;
    box-shadow:4px 2px 4px black;
   text-decoration:none;
      background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
 
  }
  span{
    cursor:pointer;
 margin-left:30px;
 margin-right:15px;
   font-size:20px;
   color:grey;
  }
  span:hover{
    opacity:0.5;
    
    text-decoration:none;
   color:white;
    margin-top:60px;
 
      background: linear-gradient(to right, #000000, #434343);
  }
  Link{
       text-decoration:none;
  }